<!--  - 3 - $router.id  -->
<template>
    <div id="service_furnichure_router">
        <!--   -->
        <mu-appbar >
            <!-- '' -->
            <mu-icon-button icon='arrow_back' slot="left" @click="returnFurnichure()" />
            </mu-icon-button>
            <h2> {{ title }} </h2>
            <mu-icon-button icon='sms' slot="right"/>
        </mu-appbar>
        <!--  -->
        <Swiper :swiper_arr=furnichureInfo_Img_Arr  />
        <div class="furnichureTitle">
            <h3> {{ furnichureInfo.name }} </h3>
            <p> {{ furnichureInfo.price }} </p>
            <!--  -->
            <mu-checkbox class="demo-checkbox" uncheckIcon="favorite_border" checkedIcon="favorite" />
            <!--<mu-checkbox class="demo-checkbox" uncheckIcon="visibility_off" checkedIcon="visibility"/>-->
        </div>
        <!--  -->
        <div class="furnichure--box furnichure__Describe">
            <Title :title_info = describe_title />
            <p> {{ furnichureInfo.description }} </p>
        </div>
        <!--  -->
        <div class="furnichure--box">
            <Title :title_info = size_title />
            <!--  -->
        </div>
        <!--  -->
        <div class="furnichure--box">
            <Title :title_info = custom_title />
            <div>
                <p> 1.  {{ custom_List[0].text }} </p>
                <p> 2.  {{ custom_List[1].text }} </p>
                <p> 3.  <b> {{ custom_List[2].text }} </b> </p>
            </div>
        </div>
        <!--  -->
        <div class="furnichure--box">
            <Title :title_info = flow_title />
            <img :src="flow_img.imgUrl" />
        </div>
        <!--  -->
        <div class="furnichure--box">
            <Title :title_info = Notes_title />
            <div v-for="(item, index) in Notes_content">
                <p> {{ index + 1 }} . {{ item.text }} </p>
            </div>
        </div>
    </div>
</template>

<script>
import      { mapActions, mapGetters } from 'vuex'
import      Title           from    '../Auto/Title.vue'
import      Swiper          from    '../Discover/Swiper.vue'     // 
const       components = { Swiper,Title }

export default {
    data() {
        return {
            title: this.$route.params.id,
            describe_title: {
                title: '',
                backgroundColor: '#FFF'
            },
            size_title: {
                title: '',
                backgroundColor: '#FFF'
            },
            custom_title: {
                title: '',
                backgroundColor: '#FFF'
            },
            custom_List: [
                { text: '' },
                { text: '' },
                { text: '0532-83861532' }
            ],
            flow_title: {
                title: '',
                backgroundColor: '#FFF'
            },
            flow_img: {
                imgUrl: require('../../assets/images/instruction.png')
            },
            Notes_title: {
                title: '',
                backgroundColor: '#FFF'
            },
            Notes_content: [
                { text: '' },
                { text: '' },
                { text: '' },
                { text: '5000' }
            ]
        }
    },
    mounted: function() {
        this.addAppOnClick()                                                 // App
        this.getFurnichureInfo()                                             // 
    }
    ,methods: {
        // : 
        ...mapActions([ 'getFurnichureInfo' ])                                 // 
        // :  (  '' )
        ,returnFurnichure: () => {
            location.href='#/service/furnichure'
        }
        // : App(  /  )
        ,addAppOnClick: () => {
            setTimeout(() => {
                // console.log('')
                const btnLeft   = document.getElementsByClassName('left')[0]        //  returnBtn()
                const btnRight  = document.getElementsByClassName('right')[0]       //  dialogueBtn() 
                btnLeft.setAttribute("onClick", "returnBtn()")
                btnRight.setAttribute("onClick", "dialogueBtn()")
            },1000)
        }
    }
    ,computed: mapGetters({ furnichureInfo_Img_Arr: 'furnichureInfo_Img_Arr', furnichureInfo: 'furnichureInfo' })
    ,components: components
}
</script>

<style lang="sass?indentedSyntax">
@import '../../sass/main.sass'

#service_furnichure_router
    /* MuseUI - App Bar */
    .mu-paper-1
        top: 0 !important
        bottom: initial !important
        box-shadow: 0 1px 2px rgba(0,0,0,.117647), 0 1px 3px rgba(0,0,0,.117647) !important
        +bC($F)
        .mu-icon
            color: $badgeFont-color !important
        h2
            color: $badgeFont-color !important
            text-align: center
</style>



